        setStopWord: function () {
            let self = this
            if (self.selectedItemList.length === 0) {
                //console.log('no delete')
                self.$Message.warning('无待设置项!');
            } else {
                let data2Update = []
                let myData = [] // 传送到 StopDict 用 
                for (let element in self.selectedItemList) {
                    data2Update.push(self.selectedItemList[element]['_id']['$oid'])
                    myData.push({
                        'word': self.selectedItemList[element].word,
                        'operator': localStorage.getItem('kwmUser'),
                        'source': 'extendedWords'
                    })
                }
                console.log(data2Update, myData)
                // 1- 首先将该词 添加到 停止词 词典
                self.axios({
                        method: 'post',
                        url: self.baseurl + 'StopDict/' + self.currentComponent,
                        data: myData
                    })
                    .then(res => {
                        console.log(res)
                        // 如果成功，则改变 该词 在当前表中的状态， !!!!!!!先用比较弱鸡的 方法写，前端循环发送。等到后期 再改成 一次发送吧, !!!!!!!!!!!
                        // 循环吧，无奈啊
                        for (let ele in data2Update) {
                            let uid = data2Update[ele]
                            let myData = {
                                'status': '停止'
                            }
                            let Params = {
                                'currentPage': self.currentPage,
                                'pageSize': self.pageSize
                            }
                            self.axios({
                                    method: 'patch',
                                    url: self.baseurl + 'extendedWords/' + self.currentComponent + '/' + uid,
                                    data: myData,
                                    params: Params

                                })
                                .then(res => {
                                    // console.log(res)
                                    // self.currentPage = 1
                                    if (res.data.count !== '') {
                                        self.itemCount = res.data.count
                                    }
                                    self.extendedWordsItemData = res.data.content
                                    self.selectedItemList = []
                                    // console.log(self.extendedWordsItemData)
                                })
                                .catch(err => {
                                    // console.log(err)
                                    console.log(err.response.data.detail)
                                    self.$Message.error(err.response.data.detail);
                                })
                        }

                    })
                    .catch(err => {
                        console.log(err)
                    })
            }
        },
